<template>
  <v-container fluid>
    <v-data-table
      :headers="headers"
      :items="topics"
      :search="search"
      v-model="selected"
      show-select
      class="elevation-2 ma-2"
      :loading="loading"
      loading-text="Loading... Please wait"
    >
      <!-- 添加按钮及弹出对话框 -->
      <template v-slot:top>
        <v-toolbar flat>
          <v-toolbar-title>文章管理</v-toolbar-title>
          <v-divider class="mx-4" inset vertical>
            <!-- vertical 用于指定垂直方向的分隔线 -->
          </v-divider>
          <v-spacer></v-spacer>
          <!-- 搜索文本框 -->
          <v-text-field
            v-model="search"
            append-icon="mdi-magnify"
            label="Search"
            single-line
            hide-details
            class="mx-4"
          ></v-text-field>
          <!-- 添加 和 修改 对话框 start -->
          <v-dialog v-model="dialog" :fullscreen="fullscreen" max-width="900px">
            <!-- dialog 内的 template 为触发对话框的元素，例如按钮 -->
            <template v-slot:activator="{ on, attrs }">
              <v-btn color="success" dark class="mb-2" v-bind="attrs" v-on="on">
                <v-icon>mdi-plus</v-icon>
                New Item
              </v-btn>
            </template>
            <!-- dialog 内的 </template> 之后为对话框显示的内容 -->
            <v-card>
              <!-- 对话框的标题在 添加 和 修改 时显示不同的颜色 -->
              <v-toolbar dark class="primary white--text">
                <v-tooltip bottom v-if="fullscreen">
                  <template v-slot:activator="{ on, attrs }">
                    <v-btn
                      v-bind="attrs"
                      v-on="on"
                      icon
                      dark
                      @click="fullscreen = false"
                    >
                      <v-icon>mdi-vector-arrange-below</v-icon>
                    </v-btn>
                  </template>
                  <span>还原</span>
                </v-tooltip>

                <v-tooltip bottom v-else>
                  <template v-slot:activator="{ on, attrs }">
                    <v-btn
                      v-bind="attrs"
                      v-on="on"
                      icon
                      dark
                      @click="fullscreen = true"
                    >
                      <v-icon> mdi-square-outline</v-icon>
                    </v-btn>
                  </template>
                  <span>最大化</span>
                </v-tooltip>

                <!-- 
                <v-btn title="全屏" v-else icon dark @click="fullscreen = true">
                  <v-icon> mdi-square-outline</v-icon>
                </v-btn> -->

                <span class="text-h5 mx-4">{{ formTitle }}</span>

                <v-spacer></v-spacer>
                <v-btn class="white--text mx-4" text @click="close">
                  Cancel
                </v-btn>
                <v-btn class="success darken-1" @click="save"> Save </v-btn>
              </v-toolbar>

              <v-card-text>
                <v-container>
                  <v-row>
                    <v-col :cols="formCols">
                      <v-select
                        :items="items"
                        v-model="editedItem.category"
                        label="文章分类"
                      ></v-select>
                    </v-col>
                    <v-col cols="2" v-if="editedIndex > -1">
                      <!-- 只有在修改时才显示的 level 数字框， type="number"用于显示数字微调 -->
                      <v-text-field
                        v-model="editedItem.level"
                        label="Level"
                        value="0"
                        type="number"
                      ></v-text-field>
                    </v-col>
                    <v-col cols="12">
                      <!-- Title输入框 -->
                      <v-text-field
                        v-model="editedItem.title"
                        label="Topic Title"
                        :counter="70"
                        clearable
                      ></v-text-field>
                    </v-col>
                    <!-- </v-row>
                  <v-row> -->
                    <v-col cols="12">
                      <span>支持 Markdown </span>
                      <!-- 多行文本框 -->
                      <v-textarea
                        :rows="rows"
                        v-model="editedItem.content"
                        label="Topic Content"
                        counter
                        outlined
                      ></v-textarea>
                    </v-col>
                    <v-col cols="12">
                      <span>多个标签用分号隔开 </span>
                      <!-- Title输入框 -->
                      <v-text-field
                        v-model="editedItem.tags"
                        label="Tags"
                        :counter="50"
                        clearable
                      ></v-text-field>
                    </v-col>
                  </v-row>
                </v-container>
              </v-card-text>
            </v-card>
          </v-dialog>
          <!-- 添加 和 修改 对话框 end -->
        </v-toolbar>
      </template>
      <!-- 添加按钮及弹出对话框 结束 -->

      <!-- 查看/回复 列 -->
      <template v-slot:[`item.count`]="{ item }">
        {{ item.views }}/{{ item.reply }}
      </template>
      <!-- 查看/回复 列 结束 -->
      <!-- actions 列 -->
      <template v-slot:[`item.actions`]="{ item }">
        <v-icon
          dense
          class="mr-2"
          color="amber"
          @click="editItem(item)"
          title="Edit Item"
        >
          mdi-pencil
        </v-icon>
        <v-icon
          color="red darken-2"
          dense
          @click="deleteItem(item)"
          title="Delete Item"
        >
          mdi-delete
        </v-icon>
      </template>
      <!-- actions 列  结束-->
    </v-data-table>
  </v-container>
</template>

<script>
import moment from "moment"; //日期处理类库
import CategoryService from "@/service/category_service.js";
import TopicService from "@/service/topic_service.js";
export default {
  name: "topic-manager",
  data: () => ({
    headers: [
      {
        text: "Category",
        value: "category.text",
        sortable: true,
        align: "start",
      },
      { text: "Title", value: "title", sortable: true, align: "start" },
      {
        text: "LastEditor",
        value: "lastEditor.username",
        sortable: true,
        width: 120,
      },
      {
        text: "Level",
        value: "level",
        sortable: true,
        width: 80,
        align: "center",
      },
      {
        text: "Views/Reply",
        value: "count",
        sortable: false,
        width: 90,
        align: "center",
      },
      { text: "CreatedAt", value: "createdAt", sortable: true, width: 130 },
      { text: "UpdatedAt", value: "updatedAt", sortable: true, width: 130 },
      {
        text: "Actions",
        value: "actions",
        sortable: false,
        width: 80,
        align: "center",
      },
    ],
    items: [], // 文章分类的标题
    categories: [], // 文章分类
    topics: [], // 所有文章
    selected: [], //多选框选中的项
    search: "", // 用于搜索功能，分别绑定到搜索框和数据表组件
    editedIndex: -1, // 用于判断 添加 或 修改 的标志；-1表示添加，否则为在 categories中的索引
    // 添加或修改绑定到对话框上的数据
    editedItem: {
      title: "",
      category: "",
      content: "",
      level: 0,
      tags: "",
    },
    // 用于添加或修改结束后，还原对话框的初始值
    defaultItem: {
      title: "",
      category: "",
      content: "",
      level: 0,
      tags: "",
    },
    currentCategory: "",
    fullscreen: false,
    loading: false, // 显示正在载入的进度条
    dialog: false, // 用于显示 添加 或 修改 对话框
    dialogDelete: false, // 用于显示 删除确认 对话框
  }),
  mounted() {
    moment.locale("zh-cn"); // 设置日期地区
    this.getCategory();
    this.getTopics();
  },
  methods: {
    getCategory() {
      this.loading = true; // 显示载入进度条
      this.items = [];
      CategoryService.fetchAll("level")
        .then((resp) => {
          if (resp.length > 0) {
            this.items = resp.map((item) => {
              return {
                text: item.get("title"),
                value: item.id,
              };
            });
            // 保留原始的 分类数据 用于添加文章时 获取相应的分类
            this.categories = resp;
            this.loading = false; // 停止进度条显示
          }
        })
        .catch((error) => {
          this.loading = false; // 停止进度条显示
        });
    },

    getTopics() {
      TopicService.fetchAll(["category", "createdBy", "lastEditor"])
        .then((resp) => {
          this.topics = resp.map((item) => this.formJson(item));
        })
        .catch((error) => {});
    },

    save() {
      if (this.editedIndex > -1) {
        // 修改记录
        // 更新当前显示数据中的 updatedAt，leancloud中的updatedAt 会自动更新，不用上传到服务器
        this.editedItem.updatedAt = moment(Date.now()).format("lll");
        // 网页显示用的用户数据
        this.editedItem.lastEditor = this.getUser(TopicService.currentUser());
        // 更新修改后内容的显示
        let item = Object.assign(
          this.topics[this.editedIndex],
          this.editedItem
        );
        // 匹配文章分类
        let categoryId = this.editedItem.category.value;
        var category = this.categories.find((item) => item.id === categoryId);

        TopicService.update(item.id, {
          category: category,
          title: item.title,
          content: item.content,
          level: Number(item.level), // 转为数字类型
          lastEditor: TopicService.currentUser(), // 上传到服务器端的用户数据
          tags: item.tags,
        });
      } else {
        //  添加记录 （此处功能不完整，添加文章后，相应分类的　count 要加一）
        let categoryId = this.editedItem.category;
        var category = this.categories.find((item) => item.id === categoryId);
        if (categoryId != "") {
          let topic = {
            category: category,
            title: this.editedItem.title.trim(),
            content: this.editedItem.content,
            level: 0,
            views: 0,
            reply: 0,
            createdBy: TopicService.currentUser(), // 分类创建者
            lastEditor: TopicService.currentUser(), // 最后的修改者
            tags: this.editedItem.tags,
          };

          console.log(topic);

          if (topic.title != "" && topic.content != "") {
            TopicService.create(topic)
              .then((resp) => {
                console.log("ok");
                this.topics.unshift(this.formJson(resp));
              })
              .catch((error) => {});
          }
        }
      }
      this.close();
    },

    editItem(item) {
      console.log("editItems: ", this.items);
      // 查询要修改记录在 categories 中的索引值
      this.editedIndex = this.topics.indexOf(item);
      this.editedItem = Object.assign({}, item);
      console.log("editItem: ", this.editedItem);
      // // 用于修改对话框的分类下位框匹配当前项
      // this.currentCategory = item.get("category").get("title");
      this.dialog = true;
    },

    deleteItem(item) {
      //
    },

    formJson(item) {
      return {
        id: item.id,
        category: item.get("category")
          ? {
              // 适用下拉框的分类
              text: item.get("category").get("title"),
              value: item.get("category").id,
            }
          : "",
        title: item.get("title"),
        content: item.get("content"),
        level: item.get("level"),
        views: item.get("views"),
        reply: item.get("reply"),
        tags: item.get("tags"),
        createdAt: moment(item.createdAt).format("lll"),
        updatedAt: moment(item.updatedAt).format("lll"),
        createdBy: item.get("createdBy")
          ? this.getUser(item.get("createdBy"))
          : "",
        lastEditor: item.get("lastEditor")
          ? this.getUser(item.get("lastEditor"))
          : "",
      };
    },

    // 获取用户表中各个字段的值
    getUser(user) {
      // console.log(user);
      return {
        id: user.id,
        username: user.get("username"),
        email: user.get("email"),
      };
    },

    // 关闭对话框，并还原对话框数据为初始值
    close() {
      this.dialog = false; // 关闭对话框
      // this.$nextTick()将回调延迟到下次 DOM 更新循环之后执行
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      });
    },
  },

  // 计算属性：函数名当作变量使用
  computed: {
    // 添加或修改对话框的标题文字
    formTitle() {
      return this.editedIndex === -1 ? "New Item" : "Edit Item";
    },
    // 添加 或 修改 时 对话框 Title 的宽度
    // 添加时不用显示 Level，Title 宽度为 cols="12"
    // 修改时需要显示 Level，Title 宽度为 cols="10"
    formCols() {
      return this.editedIndex === -1 ? 12 : 10;
    },
    // 多行文本框全屏时 12 行，否则 6 行
    rows() {
      return this.fullscreen ? 12 : 6;
    },
  },

  // 观察属性用于监控变量的值是否发生变化
  watch: {
    // 监控 data 中的变量：dialog，val 为有变化后的值
    dialog(val) {
      val || this.close(); // 如果是对话框以外空白的地方关闭对话框时，还原对话框的状态
    },

    dialogDelete(val) {
      val || this.close(); // 如果是对话框以外空白的地方关闭对话框时，还原对话框的状态
    },
  },
};
</script>

<style>
</style>